@use 'variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$mainFont;
    font-size: 1.6rem;
    font-weight: 300;
}

.container {
    width: 88%;
    max-width: 1440px;
    margin: 0 auto;

    @include m.tablet {
        width: 95%;
    }
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

ul {
    padding:0;
    list-style: none;
    margin: 0;
}

.block-scroll {
    overflow: hidden;
}

.center-box {
    margin: 0 auto;
}