@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopxl {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}

@mixin desktopxxl {
    @media (min-width: v.$desktopxxl) {
        @content;
    }
}

@mixin ultra {
    @media (min-width: v.$ultra) {
        @content;
    }
}

/* Buttons */
@mixin btn {
    display: block;
    border: .2rem solid v.$black;
    padding: 1rem 3.6rem;
    width: fit-content;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-family: v.$secondaryFont;
    letter-spacing: .5rem;
    margin: 0 auto;
    transition-property: background-color, color;
    transition-duration: .1s;
    transition-timing-function: ease-in-out;

    @include tablet {
        border: .2rem solid v.$black;
        padding: .75rem 3rem;
        letter-spacing: .45rem;
    }

    @include desktopxl {
        padding: 1rem 4.4rem;
        font-size: 1.6rem;
    }

    @include desktopxxl {
        font-size: 1.8rem;
        padding: 1.25rem 4.7rem;
        letter-spacing: .6rem;
    }
    
    &:hover {
        background-color: v.$black;
        color: v.$white;
    }
}

/* Grid */
@mixin grid($columns, $spacing ) {
    display: grid;
    gap: $spacing;
    grid-template-columns: repeat($columns, 1fr);
}

/* Headings and paragraphs */
@mixin heading {
    font-size: 3.2rem;
    text-transform: uppercase;
    text-align: center;
    line-height: 1;

    @include tablet {
        font-size: 4.5rem;
    }

    @include desktopxl {
        font-size: 5.4rem;
    }

    @include desktopxxl {
        font-size: 6.4rem;
    }
}

@mixin subheading {
    width: 87%;
    margin: 0 auto;
    text-align: center;
    color: v.$lightGray;

    @include phone {
        font-size: 2rem;
        max-width: 93rem;
    }

    @include desktop {
        font-size: 2.4rem;
    }
}

@mixin underlined {
    &::after {
        position: absolute;
        top: 2.5rem;
        left: 0;
        right: 0;
        bottom: 0;

        display: block;
        content: '';
        background-color: v.$white;
        height: .25rem;
        width: 3rem;
        margin: 0 auto;

        opacity: 0;
        transition: opacity 100ms ease-in-out;

        @include tablet {
            top: 3.7rem;
        }
    }
  
    &:hover {
        cursor: pointer;
        
        &::after {
            opacity: 1;
        }
    }
}