@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin-top: 10rem;
    padding: 6rem 0;
    background-color: v.$black;

    @include m.tablet {
        margin-top: 23rem;
    }

    &__logo {
        width: 14.5rem;
        
        @include m.tablet {
            width: 18rem;
        }
    }

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: stretch;
        }
    }

    &__navigation {
        margin-top: 4rem;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2.4rem;

        @include m.tablet {
            flex-direction: row;
            margin-bottom: .5rem;
        }

        @include m.desktop {
            gap: 4.5rem;
        }
    }

    &__navigation-link {
        position: relative;
        font-size: 1.5rem;
        font-family: v.$secondaryFont;
        color: v.$white;
        @include m.underlined;

        @include m.tablet {
            top: 1rem;
            font-size: 1.75rem;
            text-transform: capitalize;
            font-family: v.$secondaryFont;
        }

        @include m.desktop {
            font-size: 1.9rem;
        }
    }

    &__box:nth-child(2) {
        @include m.tablet {
            display:flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }

    &__copyright {
        margin-bottom: 0;
        font-size: 1.5rem;
        font-family: v.$secondaryFont;
        color: v.$darkGray;

        @include m.tablet {
            font-size: 1.75rem;
        }
    }
}

.social {
    margin-top: 5rem;
    height: 2.5rem;
    
    display: flex;
    justify-content: center;
    gap: 1.8rem;

    @include m.tablet {
        margin: 0;
        justify-content: flex-end;
        gap: 2.2rem;
    }
    
    @include m.desktopxl {
        height: 3.2rem;
    }

    &__link::before {
        content: '';
        display: block;
        background-repeat: no-repeat;
        height: 2.3rem;
        width: 2.3rem;
        background-size: 2.3rem;

        @include m.tablet {
            height: 3rem;
            width: 3rem;
            background-size: 3rem;
        }
    }
    
    &__link[href*="facebook.com"]::before {
        background-image: url(../img/icon-facebook.svg);
    }
    &__link[href*="twitter.com"]::before {
        background-image: url(../img/icon-twitter.svg);
    }
    &__link[href*="pinterest.com"]::before {
        background-image: url(../img/icon-pinterest.svg);
    }
    &__link[href*="instagram.com"]::before {
        background-image: url(../img/icon-instagram.svg);
        margin-right: 0;
    }

    &__link {
        position: relative;

        &::after {
            position: absolute;
            top: 3.2rem;
            left: 0;
            right: 0;
            bottom: 0;
    
            display: block;
            content: '';
            background-color: v.$white;
            height: .25rem;
            width: 2rem;
            margin: 0 auto;
    
            opacity: 0;
            transition: opacity 100ms ease-in-out;
    
            @include m.tablet {
                width: 3rem;
                top: 4rem;
            }
        }

        &:hover {
            cursor: pointer;
            
            &::after {
                opacity: 1;
            }
        }
    }

    &__accessible { 
        display: block;
        width: 1px;
        height: 1px;
        padding: 0;
        border: 0;
        margin: 0;
        overflow: hidden;
    }
}

.attribution {
    padding: 1rem 0;
    background-color: #272727;

    &__grid {
        display: flex;
        justify-content: center;
        gap: 2rem;
    }
    
    &__text {
        display: inline;
        font-size: 1rem;
        text-align: center;
        color: #9b9b9b;
    }

    a {
        color: hsl(228, 45%, 44%);
        font-size: 1rem;
        color: v.$white;
    }
}
