body {
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.container {
  width: 88%;
  max-width: 1440px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 95%;
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.block-scroll {
  overflow: hidden;
}

.center-box {
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  font-weight: 400;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
  color: #000000;
}

p {
  font-size: 1.6rem;
  line-height: 1.6;
}

.header {
  min-height: 65rem;
  padding: 4rem 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .header {
    min-height: 85rem;
  }
}

.notwebp.notavif .header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.2) 100%), url(../img/mobile/image-hero.jpg);
}
@media (min-width: 768px) {
  .notwebp.notavif .header {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url(../img/desktop/image-hero.jpg);
  }
}

.webp .header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.2) 100%), url(../img/mobile/image-hero.webp);
}
@media (min-width: 768px) {
  .webp .header {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url(../img/desktop/image-hero.webp);
  }
}

.avif .header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.2) 100%), url(../img/mobile/image-hero.avif);
}
@media (min-width: 768px) {
  .avif .header {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url(../img/desktop/image-hero.avif);
  }
}

@media (min-width: 768px) {
  .header__mobile-menu {
    visibility: hidden;
  }
}
.header__barra {
  visibility: hidden;
  transition: opacity 100ms linear;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000000;
  padding: 4rem 2.3rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__barra {
    visibility: visible;
    position: static;
    background-color: transparent;
    padding: 0;
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .mostrar {
  visibility: visible;
  opacity: 1;
}
.header .ocultar {
  opacity: 0;
}
.header__logo {
  width: 14.5rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 20rem;
  }
}
@media (min-width: 992px) {
  .header__logo {
    width: 25rem;
  }
}
.header__menu, .header__mobile-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__icon {
  width: 2rem;
}
@media (min-width: 768px) {
  .header__icon {
    display: none;
  }
}
.header__icon-open, .header__icon-close {
  cursor: pointer;
}
.header__heading {
  margin-top: 16rem;
  border: 0.2rem solid #ffffff;
  padding: 2.5rem 2.5rem 1.6rem 2.5rem;
  text-transform: uppercase;
  line-height: 1;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header__heading {
    padding: 3.75rem 3.75rem 2.6rem 3.75rem;
    font-size: 6.5rem;
    max-width: 65rem;
  }
}
@media (min-width: 992px) {
  .header__heading {
    padding: 5rem 5rem 3rem 5rem;
    font-size: 9.5rem;
    max-width: 85rem;
  }
}

.navigation {
  margin-top: 17rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navigation {
    margin-top: 0;
    flex-direction: row;
    align-items: center;
  }
}
@media (min-width: 992px) {
  .navigation {
    gap: 4.5rem;
  }
}
.navigation__link {
  position: relative;
  text-transform: uppercase;
  font-size: 2.4rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__link {
    font-size: 1.75rem;
    text-transform: capitalize;
    font-family: "Alata", sans-serif;
  }
}
@media (min-width: 992px) {
  .navigation__link {
    font-size: 1.9rem;
  }
}
.navigation__link::after {
  position: absolute;
  top: 2.5rem;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  content: "";
  background-color: #ffffff;
  height: 0.25rem;
  width: 3rem;
  margin: 0 auto;
  opacity: 0;
  transition: opacity 100ms ease-in-out;
}
@media (min-width: 768px) {
  .navigation__link::after {
    top: 3.7rem;
  }
}
.navigation__link:hover {
  cursor: pointer;
}
.navigation__link:hover::after {
  opacity: 1;
}

.leader {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .leader {
    margin-top: 15rem;
  }
}
@media (min-width: 1200px) {
  .leader {
    margin-top: 20rem;
  }
}
@media (min-width: 768px) {
  .leader__grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .leader__box-image {
    grid-column: 1/9;
    grid-row: 1/4;
  }
}
.leader__image {
  object-fit: cover;
  height: 100%;
}
.leader__content {
  padding: 5rem 2rem;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .leader__content {
    padding: 4rem 0 0 4rem;
    grid-column: 6/13;
    grid-row: 2/4;
  }
}
@media (min-width: 992px) {
  .leader__content {
    padding: 8rem 0 0 8rem;
    grid-column: 7/13;
  }
}
@media (min-width: 1200px) {
  .leader__content {
    padding: 10rem 0 0 10rem;
  }
}
@media (min-width: 1400px) {
  .leader__content {
    padding: 12rem 0 0 12rem;
  }
}
.leader__heading {
  font-size: 3.2rem;
  text-transform: uppercase;
  text-align: center;
  line-height: 1;
}
@media (min-width: 768px) {
  .leader__heading {
    font-size: 4.5rem;
  }
}
@media (min-width: 1200px) {
  .leader__heading {
    font-size: 5.4rem;
  }
}
@media (min-width: 1400px) {
  .leader__heading {
    font-size: 6.4rem;
  }
}
@media (min-width: 768px) {
  .leader__heading {
    text-align: left;
  }
}
@media (min-width: 992px) {
  .leader__heading {
    margin-bottom: 1rem;
  }
}
.leader__text {
  margin-bottom: 0;
  font-family: "Alata", sans-serif;
  font-size: 1.5rem;
  line-height: 1.7;
  text-align: center;
  color: #8c8c8c;
}
@media (min-width: 768px) {
  .leader__text {
    font-size: 1.75rem;
    text-align: left;
  }
}
@media (min-width: 992px) {
  .leader__text {
    font-size: 2rem;
  }
}

.creations {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .creations {
    margin-top: 24rem;
  }
}
.creations__box-heading {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .creations__box-heading {
    margin-bottom: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}
.creations__heading {
  font-size: 3.2rem;
  text-transform: uppercase;
  text-align: center;
  line-height: 1;
  text-align: center;
}
@media (min-width: 768px) {
  .creations__heading {
    font-size: 4.5rem;
  }
}
@media (min-width: 1200px) {
  .creations__heading {
    font-size: 5.4rem;
  }
}
@media (min-width: 1400px) {
  .creations__heading {
    font-size: 6.4rem;
  }
}
@media (min-width: 768px) {
  .creations__heading {
    text-align: left;
  }
}
.creations__btn-inline {
  display: none;
}
@media (min-width: 768px) {
  .creations__btn-inline {
    display: block;
    border: 0.2rem solid #000000;
    padding: 1rem 3.6rem;
    width: fit-content;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-family: "Alata", sans-serif;
    letter-spacing: 0.5rem;
    margin: 0 auto;
    transition-property: background-color, color;
    transition-duration: 0.1s;
    transition-timing-function: ease-in-out;
    margin: 0;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .creations__btn-inline {
    border: 0.2rem solid #000000;
    padding: 0.75rem 3rem;
    letter-spacing: 0.45rem;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .creations__btn-inline {
    padding: 1rem 4.4rem;
    font-size: 1.6rem;
  }
}
@media (min-width: 768px) and (min-width: 1400px) {
  .creations__btn-inline {
    font-size: 1.8rem;
    padding: 1.25rem 4.7rem;
    letter-spacing: 0.6rem;
  }
}
@media (min-width: 768px) {
  .creations__btn-inline:hover {
    background-color: #000000;
    color: #ffffff;
  }
}
.creations__grid {
  display: grid;
  grid-template-rows: repeat(8, 12rem);
  gap: 2rem;
  margin-bottom: 3.5rem;
}
@media (min-width: 768px) {
  .creations__grid {
    grid-template-columns: repeat(3, minmax(200px, 1fr));
    grid-template-rows: repeat(1, 60rem);
    grid-auto-rows: 60rem;
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .creations__grid {
    grid-template-columns: repeat(4, minmax(250px, 1fr));
    gap: 4rem;
  }
}
.creations__btn {
  display: block;
  border: 0.2rem solid #000000;
  padding: 1rem 3.6rem;
  width: fit-content;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-family: "Alata", sans-serif;
  letter-spacing: 0.5rem;
  margin: 0 auto;
  transition-property: background-color, color;
  transition-duration: 0.1s;
  transition-timing-function: ease-in-out;
}
@media (min-width: 768px) {
  .creations__btn {
    border: 0.2rem solid #000000;
    padding: 0.75rem 3rem;
    letter-spacing: 0.45rem;
  }
}
@media (min-width: 1200px) {
  .creations__btn {
    padding: 1rem 4.4rem;
    font-size: 1.6rem;
  }
}
@media (min-width: 1400px) {
  .creations__btn {
    font-size: 1.8rem;
    padding: 1.25rem 4.7rem;
    letter-spacing: 0.6rem;
  }
}
.creations__btn:hover {
  background-color: #000000;
  color: #ffffff;
}
@media (min-width: 768px) {
  .creations__btn {
    display: none;
  }
}

