.social__link::before {
  content: "";
  display: block;
  background-repeat: no-repeat;
  height: 2.3rem;
  width: 2.3rem;
  background-size: 2.3rem;
}
@media (min-width: 768px) {
  .social__link::before {
    height: 3rem;
    width: 3rem;
    background-size: 3rem;
  }
}
.social__link[href*="facebook.com"]::before {
  background-image: url(../img/icon-facebook.svg);
}
.social__link[href*="twitter.com"]::before {
  background-image: url(../img/icon-twitter.svg);
}
.social__link[href*="pinterest.com"]::before {
  background-image: url(../img/icon-pinterest.svg);
}
.social__link[href*="instagram.com"]::before {
  background-image: url(../img/icon-instagram.svg);
  margin-right: 0;
}
.social__link {
  position: relative;
}
.social__link::after {
  position: absolute;
  top: 3.2rem;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  content: "";
  background-color: #ffffff;
  height: 0.25rem;
  width: 2rem;
  margin: 0 auto;
  opacity: 0;
  transition: opacity 100ms ease-in-out;
}
@media (min-width: 768px) {
  .social__link::after {
    width: 3rem;
    top: 4rem;
  }
}
.social__link:hover {
  cursor: pointer;
}
.social__link:hover::after {
  opacity: 1;
}
.social__accessible {
  display: block;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: 0;
  overflow: hidden;
}

