@use 'base/variables' as v;
@use 'base/mixins' as m;

.creations {
    margin-top: 5rem;

    @include m.tablet {
        margin-top: 24rem;
    }

    &__box-heading {
        margin-bottom: 5rem;
        
        @include m.tablet {
            margin-bottom: 10rem;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
        }
    }

    &__heading {
        @include m.heading;
        text-align: center;

        @include m.tablet {
            text-align: left;
        }
    }

    &__btn-inline {
        display: none;

        @include m.tablet {
            @include m.btn;
            margin: 0;
        }
    }

    &__grid {
        display: grid;
        grid-template-rows: repeat(8, 12rem);
        gap: 2rem;
        margin-bottom: 3.5rem;
        
        @include m.tablet {
            grid-template-columns: repeat(3, minmax(200px, 1fr));
            grid-template-rows: repeat(1, 60rem);
            grid-auto-rows: 60rem;
            gap: 2rem;
        }

        @include m.desktopxl {
            grid-template-columns: repeat(4, minmax(250px, 1fr));
            gap: 4rem;
        }
    }

    &__btn {
        @include m.btn;

        @include m.tablet {
            display: none;
        }
    }
}

.creation {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
}

