.creation {
    &__gradient {
        padding: 1.6rem;
        
        display: flex;
        align-items: flex-end;
        
        width: 100%;
        height: 100%;
        background-image: linear-gradient(to right, transparentize(v.$black, .4%) 0%, transparentize(v.$black, .9%) 70%);

        @include m.tablet {
            padding: 3rem 3rem 2.5rem 3rem;
            background-image: linear-gradient(to top, transparentize(v.$black, .4%) 0%, transparentize(v.$black, .9%) 70%);
        }

        @include m.desktopxxl {
            padding: 5rem 5rem 4.5rem 5rem;
        }
    }

    &__name {
        text-transform: uppercase;
        font-size: 2.4rem;
        line-height: 1;
        color: v.$white;

        span {
            display: block;
        }

        @include m.tablet {
            font-size: 3.2rem;
        }

        @include m.desktopxl {
            font-size: 4rem;
        }
    }
    
    &:hover {
        .creation__gradient {
            background-image: linear-gradient(to right, transparentize(v.$white, .3%) 0%, transparentize(v.$white, .3%) 100%);
    
            @include m.tablet {
                background-image: linear-gradient(to top, transparentize(v.$white, .3%) 0%, transparentize(v.$white, .3%) 100%);
            }
        }

        .creation__name {
            color: v.$black;
        }
    }
}