@use 'base/variables' as v;
@use 'base/mixins' as m;

.leader {
    margin-top: 10rem;

    @include m.tablet {
        margin-top: 15rem;
    }

    @include m.desktopxl {
        margin-top: 20rem;
    }

    &__grid {
        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(12, 1fr);
            grid-template-rows: repeat(3, 1fr);
            gap: 2rem;
        }
    }
    
    &__box-image {
        @include m.tablet {
            grid-column: 1 / 9;
            grid-row: 1 / 4;
        }
    }

    &__image {
        object-fit: cover;
        height: 100%;
    }

    &__content {
        padding: 5rem 2rem;
        background-color: v.$white;

        @include m.tablet {
            padding: 4rem 0 0 4rem;
            grid-column: 6 / 13;
            grid-row: 2 / 4;
        }

        @include m.desktop {
            padding: 8rem 0 0 8rem;
            grid-column: 7 / 13;
        }

        @include m.desktopxl {
            padding: 10rem 0 0 10rem;
        }

        @include m.desktopxxl {
            padding: 12rem 0 0 12rem;
        }
    }

    &__heading {
        @include m.heading;

        @include m.tablet {
            text-align: left;
        }

        @include m.desktop {
            margin-bottom: 1rem;
        }
    }

    &__text {
        margin-bottom: 0;
        font-family: v.$secondaryFont;
        font-size: 1.5rem;
        line-height: 1.7;
        text-align: center;
        color: v.$darkGray;

        @include m.tablet {
            font-size: 1.75rem;
            text-align: left;
        }
        
        @include m.desktop {
            font-size: 2rem;
        }
    }
}