@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    min-height: 65rem;
    padding: 4rem 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include m.tablet {
        min-height: 85rem;
    }
}

.notwebp.notavif .header {
    background-image: linear-gradient(to right, rgb( 0 0 0 / .2 ) 0%, rgb( 0 0 0 / .2 ) 100%), url(../img/mobile/image-hero.jpg);

    @include m.tablet {
        background-image: linear-gradient(to right, rgb( 0 0 0 / .4 ) 0%, rgb( 0 0 0 / .4 ) 100%), url(../img/desktop/image-hero.jpg);
    }
}

.webp .header {
    background-image: linear-gradient(to right, rgb( 0 0 0 / .2 ) 0%, rgb( 0 0 0 / .2 ) 100%), url(../img/mobile/image-hero.webp);

    @include m.tablet {
        background-image: linear-gradient(to right, rgb( 0 0 0 / .4 ) 0%, rgb( 0 0 0 / .4 ) 100%), url(../img/desktop/image-hero.webp);
    }
}

.avif .header {
    background-image: linear-gradient(to right, rgb( 0 0 0 / .2 ) 0%, rgb( 0 0 0 / .2 ) 100%), url(../img/mobile/image-hero.avif);

    @include m.tablet {
        background-image: linear-gradient(to right, rgb( 0 0 0 / .4 ) 0%, rgb( 0 0 0 / .4 ) 100%), url(../img/desktop/image-hero.avif);
    }
}

.header {
    &__mobile-menu {
        @include m.tablet {
            visibility: hidden;
        }
    }

    &__barra {
        visibility: hidden;
        transition: opacity 100ms linear;

        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;

        background-color: v.$black;
        padding: 4rem 2.3rem;
        
        display: flex;
        flex-direction: column;

        @include m.tablet {
            visibility: visible;
            position: static;

            background-color: transparent;
            padding: 0;

            flex-direction: row;
            justify-content: space-between;
        }
    }

    .mostrar {
        visibility: visible;
        opacity: 1;
    }
    .ocultar {
        opacity: 0;
    }
    
    &__logo {
        width: 14.5rem;
        
        @include m.tablet {
            width: 20rem;
        }
        @include m.desktop {
            width: 25rem;
        }
    }

    &__menu,
    &__mobile-menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__icon {
        width: 2rem;
        
        @include m.tablet {
            display: none;
        }
    }

    &__icon-open,
    &__icon-close {
        cursor: pointer;
    }

    &__heading {
        margin-top: 16rem;
        border: .2rem solid v.$white;
        padding: 2.5rem 2.5rem 1.6rem 2.5rem;
        text-transform: uppercase;
        line-height: 1;
        color: v.$white;

        @include m.tablet {
            padding: 3.75rem 3.75rem 2.6rem 3.75rem;
            font-size: 6.5rem;
            max-width: 65rem;
        }

        @include m.desktop {
            padding: 5rem 5rem 3rem 5rem;
            font-size: 9.5rem;
            max-width: 85rem;
        }
    }
}