@use 'base/variables' as v;
@use 'base/mixins' as m;

.navigation {
    margin-top: 17rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;

    @include m.tablet {
        margin-top: 0;
        flex-direction: row;
        align-items: center;
    }

    @include m.desktop {
        gap: 4.5rem;
    }

    &__link {
        position: relative;
        text-transform: uppercase;
        font-size: 2.4rem;
        color: v.$white;

        @include m.tablet {
            font-size: 1.75rem;
            text-transform: capitalize;
            font-family: v.$secondaryFont;
        }

        @include m.desktop {
            font-size: 1.9rem;
        }

        @include m.underlined;
    }
}